.button {
  height: 51px;
  background-color: $azure;

  @include font-parametrs(600, 15px, 21px);
  color: $black;
  border: 0;
  border-radius: 6px;

  &:hover {
    background-color: rgba($azure, 0.7);
  }

  &:focus {
    background-color: rgba($azure, 0.3);
    color: rgba($dark-1, 0.7);
  }
}
